/* General layout */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f3f4f6;
}

.dashboard-container {
  display: flex;
  min-height: 100vh;
}

/* Sidebar */
.sidebar {
  width: 220px;
  background-color: #111827;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.sidebar h2 {
  margin-bottom: 20px;
  font-size: 22px;
}

.sidebar a {
  color: white;
  text-decoration: none;
  margin-bottom: 12px;
  padding: 10px;
  border-radius: 6px;
  transition: 0.2s;
}

.sidebar a:hover {
  background-color: #2563eb;
}

/* Main content */
.main-content {
  flex: 1;
  padding: 30px;
  background-color: #f3f4f6;
}

/* Full-page content */
.full-page {
  width: 100%;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
}

/* Forms */
.full-form input,
.full-form select,
.full-form textarea {
  display: block;
  width: 100%;
  margin-bottom: 12px;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.full-form button {
  padding: 10px 15px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.btn-primary { background-color: #2563eb; color: white; }
.btn-success { background-color: #16a34a; color: white; }
.btn-info { background-color: #0ea5e9; color: white; }

.info { color: green; margin-bottom: 10px; }
.error { color: red; margin-bottom: 10px; }

/* Cards */
.summary-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.card-summary {
  background-color: #2563eb;
  color: white;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
}

.chart-container {
  background-color: white;
  padding: 20px;
  border-radius: 12px;
}

/* Reports table */
.full-width-table {
  width: 100%;
  border-collapse: collapse;
}

.full-width-table th,
.full-width-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
}
/* Login & Register */
.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f3f4f6;
}

.auth-form {
  background-color: white;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
  width: 100%;
  max-width: 400px;
}

.auth-form h3 {
  margin-bottom: 20px;
  text-align: center;
  color: #111827;
}

.auth-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.auth-form button {
  width: 100%;
  padding: 10px;
  background-color: #2563eb;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.auth-form a {
  color: #4551ff;
  text-decoration: none;
}

.auth-form a:hover {
  text-decoration: underline;
}

